import Message from './Message'

export default {
  components: {
    Message
  },
  props: {
    messages: {
      type: Array,
      required: true,
      validator: function(messages) {
        return messages.every(message => {
          if (typeof message.time !== 'number') {
            return false
          }
          if (typeof message.date !== 'string') {
            return false
          }
          if (typeof message.body !== 'string') {
            return false
          }

          return true
        });
      }
    }
  }
}

