import Button from './Button'
import MessageModel from '../models/Message'

export default {
  components: {
    Button,
  },
  props: {
    onPost: {
      type: Function,
      required: true,
    },
    onGet: {
      type: Function,
      required: true,
    },
    onChart: {
      type: Function,
      required: true,
    }
  },
  data() {
    return {
      time: 0,
      body: '',
      canPost: true,
    }
  },
  methods: {
    async post() {
      this.canPost = false;
      try {
        const message = await MessageModel.save({
          time: Number(this.time),
          body: this.body
        });
        this.onPost(message);
        this.time = 0;
        this.body = '';
      } catch (error) {
        alert(error.message);
      }
      this.canPost = true;
    },

    async get() {
      let times = 0;
      try {
        times += await MessageModel.dbtime();
        this.onGet(times);
      } catch (error) {
        alert(error.message);
      }

      return times
    },

    async chart() {
      let vuechartData = [];
      let chartdbtime = await MessageModel.dbtime();
      try {
        if (vuechartData.length === 0) {
          vuechartData.push(chartdbtime);
        } 
        this.onChart(vuechartData[0]);
      } catch (error) {
        alert(error.message);
      }

      return vuechartData[0]
    }
  },
}
